<template>
	<Fragment>
		<form
			ref="searchBar"
			class="search-bar"
			:class="{'docked': isSearchbarDocked}"
			@submit="submit($event)"
		>
			<div class="container">
				<label
					for="search"
					class="sr-only"
				>Rechercher une musique</label>
				<input
					id="search"
					v-model="search"
					type="search"
					name="search"
					class="search-bar__input"
					placeholder="Recherche un titre, un artiste..."
				>
				<button
					type="submit"
					class="search-bar__button"
				>
					<span class="search-bar__button-text">Rechercher</span>
					<img
						src="../assets/images/search.svg"
						alt="search icon"
					>
				</button>
			</div>
			<button
				class="search-bar__open-more"
				title="recherche avancée"
				@click="openAdvencedSearch"
			>
				<svg
					width="1em"
					height="1em"
					viewBox="0 0 16 16"
					fill="currentColor"
					xmlns="http://www.w3.org/2000/svg"
				>
					<path
						fill-rule="evenodd"
						d="M9.405 1.05c-.413-1.4-2.397-1.4-2.81 0l-.1.34a1.464 1.464 0 0 1-2.105.872l-.31-.17c-1.283-.698-2.686.705-1.987 1.987l.169.311c.446.82.023 1.841-.872 2.105l-.34.1c-1.4.413-1.4 2.397 0 2.81l.34.1a1.464 1.464 0 0 1 .872 2.105l-.17.31c-.698 1.283.705 2.686 1.987 1.987l.311-.169a1.464 1.464 0 0 1 2.105.872l.1.34c.413 1.4 2.397 1.4 2.81 0l.1-.34a1.464 1.464 0 0 1 2.105-.872l.31.17c1.283.698 2.686-.705 1.987-1.987l-.169-.311a1.464 1.464 0 0 1 .872-2.105l.34-.1c1.4-.413 1.4-2.397 0-2.81l-.34-.1a1.464 1.464 0 0 1-.872-2.105l.17-.31c.698-1.283-.705-2.686-1.987-1.987l-.311.169a1.464 1.464 0 0 1-2.105-.872l-.1-.34zM8 10.93a2.929 2.929 0 1 0 0-5.86 2.929 2.929 0 0 0 0 5.858z"
					/>
				</svg>

				<span class="sr-only">recherche avancée</span>
			</button>
		</form>
		<form
			v-if="isAdvencedSearchOpen"
			class="search-bar__advenced"
			@submit="submit"
		>
			<button
				class="search-bar__close"
				@click="openAdvencedSearch"
			>
				<span class="sr-only">Close</span>
			</button>
			<DoubleSliderRange
				v-model="interpretationRange"
				:min="interpretationMin"
				:max="interpretationMax"
			>
				Dernière interprétation (en jours)
				<template #help>
					Nombre de jours depuis la dernière foi que cette musique a été jouée.<br>0 équivalent à aujourd'hui et 100 à tout le temps
				</template>
				<template #label-min-value>
					Durée minimum
				</template>
				<template #label-max-value>
					Durée maximum
				</template>
			</DoubleSliderRange>
			<DoubleSliderRange
				v-model="interpretationRange"
				:min="interpretationMin"
				:max="interpretationMax"
			>
				Nombre d'interprétation
				<template #label-min-value>
					Durée minimum
				</template>
				<template #label-max-value>
					Durée maximum
				</template>
			</DoubleSliderRange>
			<DoubleSliderRange
				v-model="score"
				:min="0"
				:max="100"
			>
				Score
				<template #label-min-value>
					Score minimum
				</template>
				<template #label-max-value>
					Score maximum
				</template>
			</DoubleSliderRange>
			<div class="search-bar__row search-bar__sm-col">
				<div class="search-bar__col-left">
					<div class="search-bar__row">
						<div class="search-bar__group">
							<input
								id="odlc"
								v-model="odlc"
								type="checkbox"
								class="search-bar__checkbox"
							>
							<label
								for="odlc"
								class="search-bar__label"
							>Officielle</label>
							<input
								id="cdlc"
								v-model="cdlc"
								type="checkbox"
								class="search-bar__checkbox"
							>
							<label
								for="cdlc"
								class="search-bar__label"
							>CDLC</label>
						</div>
						<HoverTip>
							put some text here
						</HoverTip>
					</div>
					<div class="search-bar__row">
						<div class="search-bar__group">
							<input
								id="showlight"
								v-model="showlight"
								type="checkbox"
								class="search-bar__checkbox"
							>
							<label
								for="showlight"
								class="search-bar__label"
							>Showlight</label>
						</div>
						<HoverTip>
							put some more text here
						</HoverTip>
					</div>
				</div>
				<div class="search-bar__col-right">
					<p class="search-bar__label">
						Arrangement&nbsp;:
					</p>
					<div
						v-for="ar in arrangementsList"
						:key="ar"
						class="search-bar__radio_group"
					>
						<input
							:id="'arrangement-' + ar"
							v-model="arrangement"
							type="radio"
							class="search-bar__radio"
							:value="ar"
						>
						<label
							:for="'arrangement-' + ar"
							class="search-bar__label"
						>
							{{ ar }}
						</label>
					</div>
				</div>
			</div>

			<div class="search-bar__btn-group">
				<button class="search-bar__reset search-bar__btn">
					Reset
				</button>

				<button
					type="submit"
					class="search-bar__submit search-bar__btn search-bar__btn--with-icon"
				>
					Rechercher
					<img
						class="search-bar__btn-icon"
						src="../assets/images/search.svg"
						alt="search icon"
					>
				</button>
			</div>
		</form>
	</Fragment>
</template>

<script lang="js">
import { Fragment } from 'vue-fragment';

import DoubleSliderRange from './DoubleSliderRange';
import HoverTip from './HoverTip';

export default {
	name: 'SearchBar',
	components: {
		Fragment,
		DoubleSliderRange,
		HoverTip
	},
	props: [],
	data () {
		return {
			search: '',
			isSearchbarDocked: false,
			isAdvencedSearchOpen: false,
			interpretationRange: [0, 100],
			interpretationMin: 0,
			interpretationMax: 100,
			arrangementsList: ['tous', 'rythm', 'lead', 'bass'],
			arrangement: 'tous',
			showlight: false,
			odlc: false,
			cdlc: false,
			score: [0, 100]

		};
	},
	computed: {

	},
	watch: {
		odlc(newValue) {
			this.cdlc = newValue ? false : this.cdlc;
		},
		cdlc(newValue) {
			this.odlc = newValue ? false : this.odlc;
		}
	},
	mounted() {
		const options = {
			rootMargin: '-48px 0px 10000px 0px',
			threshold: 1
		};
		const observer = new IntersectionObserver(entries => {
			entries.forEach(entry => {
				// let the advenced search open when scrolled past
				this.isSearchbarDocked = !entry.isIntersecting;

				/*
				// close the advenced search when scrolled past
				// if the bar just docked close the advenced search, if the bar undocked don't change the value
				this.isAdvencedSearchOpen = this.isSearchbarDocked ? false : this.isAdvencedSearchOpen;
				*/
			});
		}, options);

		observer.observe(this.$refs.searchBar);
	},
	methods: {
		submit(event) {
			event.preventDefault();
		},
		openAdvencedSearch() {
			if (this.isSearchbarDocked) {
				this.isAdvencedSearchOpen = true;
				this.isSearchbarDocked = false;

				this.$nextTick().then(() => {
					this.$refs.searchBar.scrollIntoView();
					document.getElementsByTagName('HTML')[0].scrollBy({
						top: -100,
						left: 0,
						behavior: 'smooth'
					});
					console.log('scroll');
				});
			}
			else {
				this.isAdvencedSearchOpen = !this.isAdvencedSearchOpen;
			}
		}
	}
};


</script>

<style lang="scss">
  .search-bar {
		top: 3em;
		padding: 0;
		margin: 0 auto 3em;
		width: 100%;
		max-width: 60ch;
		border-radius: 5px;
		overflow: hidden;
		box-shadow: 0 0 5px 1px #0003;
		transition:
			max-width 300ms cubic-bezier(.31,.45,.21,1.76),
			transform 300ms ease-in-out,
			box-shadow 300ms ease-in-out,
			background-color 300ms ease-in-out;
		transform: translateY(-50%);
		z-index: 100;
		display: flex;

		&:focus-within {
			box-shadow: 0 0 10px 3px #0003;
		}

		&__input {
			width: 100%;
			height: 100%;
			border: none;
			padding: .5em 1em;
			background-color: #fffef8;
			transition: background-color 300ms ease-in-out;
			border-radius: 5px;
			box-shadow: 0 0 5px 0 #0008;
			background-color: #999;
			&:focus {
				outline: none;
				background-color: #fff;
			}

			&::placeholder {
				color: #1d1d1d;
			}

			@at-root .docked & {
				background-color: #fff;
			}

			@supports (backdrop-filter: blur(5px)) {
				@at-root .docked & {
					backdrop-filter: blur(5px);
					background-color: #fff6;

					&:focus {
						background-color: #fff;
					}
				}
			}
		}

		&__button {
			display: flex;
			padding: .25em .5em;
			border: none;
			background: var(--primary-1);
			background: linear-gradient(18deg, var(--primary-1) 0%, var(--primary-2) 100%);
			color: var(--white);
			position: absolute;
			right: 1em;
			top: 50%;
			transform: translateY(-50%);
			border-radius: 5px;
			overflow: hidden;
			cursor: pointer;

			&-text {
				overflow: hidden;
				max-width: 0ch;
				transition: 300ms ease-out;
				transition-property: max-width, margin-right;
			}

			&:focus > &-text,
			&:hover > &-text {
				margin-right: .5em;
				max-width: 15ch;
			}

			&-logo {
				box-shadow: 0 0 5px 6px #d7d7d7;
			}
		}

		&.docked{
			max-width: 80ch;
			box-shadow: 0 0 5px #0005;
			width: calc(100vw - (2 * (50px + 2em)));
			position: sticky;
		}

		.container {
			position: relative;
			flex-grow: 1;
			z-index: 10;
		}

		&__open-more {
			border: none;
			position: relative;
			background-color: #404040;
			color: white;
			cursor: pointer;

			&::before,
			&:after {
				content: '';
				display: block;
				background-color: inherit;
				width: 5px;
				height: 5px;
				position: absolute;
				left: -5px;
			}
			&:before {
				top: 0;
				clip-path: path('M 0,0 A 5,5 0 0 1 5,5 V 0 Z');
			}
			&:after {
				bottom: 0;
				clip-path: path('M 5,0 A 5,5 0 0 1 0,5 H 5 Z');
			}
		}

		&__advenced {
			max-width: 60ch;
			margin: -3.5em auto 2em;
			border-radius: 5px;
			box-shadow: 0 0 5px 0 #0001;
			background: var(--primary-1);
			background: var(--bg-gradient);
			padding: 1em .5em;

			@media screen and (min-width: 25em) {
				padding: 1.5em;
			}
		}

		&__close {
			position: relative;
			height: 1em;
			width: 1em;
			border: 0;
			padding: 0;
			background-color: #fff0;
			cursor: pointer;
			margin-left: auto;
			display: block;
			margin-top: -.5em;

			&::before,
			&::after {
				content: '';
				position: absolute;
				display: block;
				width: .1em;
				height: 1.4em;
				border-radius: .05em;
				background-color: #fff;
			}

			&::before {
				top: 0;
				left: 0;
				transform: rotate(-45deg) translateX(-50%);
				transform-origin: top left;
			}
			&::after {
				top: 0;
				right: 0;
				transform: rotate(45deg) translateX(50%);
				transform-origin: top right;
			}
		}

		&__row {
			display: flex;
			flex-direction: row;
			gap: 1em;
			align-items: center;

			&:not(:last-child) {
				margin-bottom: .75em;
			}

		}
		&__col-left,
		&__col-right {
			width: 50%;
			display: flex;
			flex-direction: column;
		}
		@media screen and (max-width: 20em) {
			&__sm-col {
				display: flex;
				flex-direction: column;
			}
			&__col-left,
			&__col-right {
				width: 100%;
			}
		}

		&__group {
			width: 100%;
		}
		&__group &__checkbox + &__label:first-of-type {
			border-top-left-radius: 5px;
			border-top-right-radius: 5px;
		}
		&__group &__checkbox + &__label:last-of-type {
			border-bottom-left-radius: 5px;
			border-bottom-right-radius: 5px;
		}

		&__checkbox {
			position: absolute;
			width: 1px;
			height: 1px;
			padding: 0;
			margin: -1px;
			overflow: hidden;
			clip: rect(0, 0, 0, 0);
			white-space: nowrap;
			border: 0;
		}
		&__checkbox + &__label {
			cursor: pointer;
			background-color: var(--filler-1);
			padding: .5em;
			width: 100%;
			display: block;
			color: var(--text);
			text-align: center;
			font-weight: bold;
			transition: 300ms ease-in-out;
			transition-property: color, background-color;
		}

		&__checkbox:checked + &__label {
			background-color: var(--primary-2);
			color: var(--white);
		}
		&__checkbox:focus + &__label,
		&__checkbox:active + &__label {
			box-shadow: 0 0 5px 2px #0004;
			background-color: var(--filler-2);
			outline: none;
		}
		&__checkbox:checked:focus + &__label,
		&__checkbox:checked:active + &__label {
			background-color: var(--primary-1);
		}

		&__label {
			font-weight: bold;
			color: var(--text);
		}

		&__radio {
			position: absolute;
			width: 1px;
			height: 1px;
			padding: 0;
			margin: -1px;
			overflow: hidden;
			clip: rect(0, 0, 0, 0);
			white-space: nowrap;
			border: 0;
		}

		&__radio + &__label {
			position: relative;
			display: block;
			background-color: var(--filler-1);
			margin-top: .25em;
			margin-left: 2em;
			width: max-content;
			padding: .25em;
			box-shadow: var(--shadow);
			border-radius: 5px;
			cursor: pointer;

			&::before {
				content:'';
				position: absolute;
				display: block;
				width: 1em;
				height: 1em;
				background-color: var(--filler-1);
				left: -1em;
				top: 50%;
				transform: translate(-50%, -50%);
				border: 3px solid var(--filler-1);
				border-radius: 5px;
				transition: background-color 300ms ease-in-out;
			}
		}

		&__radio:checked + &__label::before {
			background-color: var(--primary-2);
		}

		&__radio:focus + &__label,
		&__radio:active + &__label {
			box-shadow: 0 0 5px 2px #0004;
			background-color: var(--filler-2);

			&::before {
				box-shadow: 0 0 5px 2px #0004;
				background-color: var(--filler-2);
			}
		}

		&__radio:checked:focus + &__label,
		&__radio:checked:active + &__label {
			box-shadow: 0 0 5px 2px #0004;
			background-color: var(--primary-1);

			&::before {
				box-shadow: 0 0 5px 2px #0004;
				background-color: var(--primary-1);
			}
		}

		&__btn-group {
			display: flex;
		}

		&__btn {
			padding: .5em 1em;
			width: 100%;
			max-width: 15ch;
			font-weight: bold;
			color: var(--text);
			border-radius: 5px;

			&-icon {
				margin-left: .5em;
			}

			&--with-icon {
				display: flex;
				flex-direction: row;
			}
		}

		&__reset {
			border: 3px solid var(--filler-2);
			background-color: #0000;

			&:focus {
				outline: 0;
				box-shadow: 0 0 5px 2px #0004, inset 0 0 5px 2px #0002;
			}
		}

		&__submit {
			background-color: var(--filler-2);
			border: 3px solid var(--filler-2);
			margin-left: auto;

			&:focus {
				outline: 0;
				box-shadow: 0 0 5px 2px #0004;
			}
		}
	}
</style>
